.rrui__expandable {
  opacity: 0;
  pointer-events: none;
  transform: scaleY(0);
  transform-origin: 50% top 0px;
  /* 250ms - too slow for short lists */
  transition: all 150ms cubic-bezier(0.76, 0.04, 0.46, 0.75) 0ms;
  /* prevent white background showing through the transparent border */
