  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px;
}

.container--full {
  max-width: 100%;
}

.container--narrow {
  max-width: 1050px;
}

@media only screen and (max-width: 568px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

button {
  display: inline-block;
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  color: white;
  transition: 0.25s ease-in-out;
}

.btn:focus, .btn:active {
  outline: none;
}

.btn--block {
  display: block;
  width: 100%;
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
}

h1, h2, h3, h4 {
  font-weight: 500;
  margin: .75em 0;
  line-height: 1.25;
}

.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
  background: white;
}
