// buttons
button {
  display: inline-block;
  -webkit-appearance: none;
  border: 0;
  box-shadow: none;
  &:focus {
    outline: none; } }

.btn {
  display: inline-block;
  position: relative;
  text-align: center;
  cursor: pointer;
  color: white;
  transition: .25s $ease;
  &:focus, &:active {
    outline: none; }
  &--block {
    display: block;
    width: 100%; } }
